<script>
export default {
  name: "Main",
  data(){
    return{
      typingStr1:"åªä¸ºå®ˆæŠ¤ä¸–ç•Œå’Œå¹³",
      typingStr2:"åªä¸ºè®©ä½ å°‘åŠ ç­",
      typingStr3:'åªä¸ºè¿™æ ·çš„ä½ ï¼š',
      // chanpinshejishi:'äº§å“è®¾è®¡å¸ˆ',
      // jiaohushejishi:'äº¤äº’è®¾è®¡å¸ˆ',
      // shijueshejishi:'è§†è§‰è®¾è®¡å¸ˆ',
      // chanpinjingli:'äº§å“ç»ç†',
      // qianduangongchengsh:'å‰ç«¯å·¥ç¨‹å¸ˆ',
      peopleType:['äº§å“è®¾è®¡å¸ˆ','äº¤äº’è®¾è®¡å¸ˆ','è§†è§‰è®¾è®¡å¸ˆ','äº§å“ç»ç†','å‰ç«¯å·¥ç¨‹å¸ˆ'],
      str1:'',
    }
  },

  beforeMount() {
		(function (doc, win) {
					var docEl = doc.documentElement,
					resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize',
					recalc = function () {
					var clientWidth = docEl.clientWidth;
					if (!clientWidth) return;
					docEl.style.fontSize = 16 * (clientWidth / 1920) + 'px';
			};
			if (!doc.addEventListener) return;
			win.addEventListener(resizeEvt, recalc, false);
			doc.addEventListener('DOMContentLoaded', recalc, false);
			})(document, window);
	},

  mounted() {
    const t = this
    let temp = 1
    t.str1 = ' '

    $('#typing1').css('height','3rem')

    //æ‰§è¡Œ8æ¬¡
    // ''--->'åªä¸ºå®ˆæŠ¤ä¸–ç•Œå’Œå¹³'
    setTimeout(function () {
      $('#typing1').css('height','auto')
      var interval = setInterval(function () {
        t.str1 = t.typingStr1.substring(0,temp)
        temp ++;
        if(temp>t.typingStr1.length){
          clearInterval(interval);
        }
      },130)
    },2500)


    //æ‰§è¡Œ6æ¬¡
    //'åªä¸ºå®ˆæŠ¤ä¸–ç•Œå’Œå¹³'--->'åªä¸º'
    setTimeout(function () {
      var interval = setInterval(function () {
        t.str1 = t.typingStr1.substring(0,temp)
        temp --;
        if(temp<2){
          clearInterval(interval);
          temp++;
        }
      },120)
    },4800)

    //æ‰§è¡Œ5æ¬¡
    //'åªä¸º'--->'åªä¸ºè®©ä½ å°‘åŠ ç­'
    setTimeout(function () {
      var interval = setInterval(function () {
        t.str1 = t.typingStr2.substring(0,temp)
        temp ++;
        if(temp>t.typingStr2.length){
          clearInterval(interval);
        }
      },150)
    },7000)

    //æ‰§è¡Œ7æ¬¡
    //'åªä¸ºè®©ä½ å°‘åŠ ç­'--->''
    setTimeout(function () {
      var interval = setInterval(function () {
        t.str1 = t.typingStr2.substring(0,temp)
        temp --;
        if(temp<0){
          clearInterval(interval);
          temp+=2;
          $('#typing1').css('height','3rem')
        }
      },120)
    },9200)

    //æ‰§è¡Œ8æ¬¡
    //''--->'åªä¸ºè¿™æ ·çš„ä½ '
    setTimeout(function () {
      $('#typing1').css('height','auto')
      var interval = setInterval(function () {
        t.str1 = t.typingStr3.substring(0,temp)
        temp ++;
        if(temp>t.typingStr3.length){
          clearInterval(interval);
        }
      },120)
    },11000)

    setTimeout(function () {
      let type = 0;
      let addFlag = true;
      let pauseFlag = false;
      temp = 0;

      var interval = setInterval(function() {
      if (!pauseFlag) {
          t.str1 = t.typingStr3 + t.peopleType[type].substring(0, temp)
          if (addFlag) {
            temp++;
            if (temp == t.peopleType[type].length+1) {
              addFlag = false
              temp --;
              pauseFlag = !pauseFlag
              setTimeout(function () {
                pauseFlag = !pauseFlag
              },2000)
            }
          } else {
            temp--;
            if (temp == -1) {
              addFlag = true
              temp++;
              pauseFlag = !pauseFlag
              setTimeout(function () {
                pauseFlag = !pauseFlag
              },2000)
              type++;
              if (type == 5) {
                type = 0
                // clearInterval(interval);
              }
            }
          }
        }
      }, 120)
    }, 13000)

  }
}
